.input-container {
  display: flex;
  justify-content: center;
  padding: 30px 0px;
}

.search-input {
  width: 400px;
  height: 50px;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  border: 2px solid #e0e7ff;
  background: #fff;
  outline: none;
  text-indent: 20px;
  transition: all 0.25s ease-out;
}

.search-input:focus {
  border-color: #05396b;
}
