* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #5cdb94;
  /* color: #05396b; */
  color: #05396b;
  transition: all 0.25s ease-out;
  font-family: "Montserrat", sans-serif;
  padding: 50px 15px;
}

.card-container:hover {
  transform: scale(1.05);
}

.btn {
  width: 120px;
  height: 40px;
  background: #05396b;
  color: #fff;
  text-align: center;
  border: none;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
  margin-top: 30px;
}
